import { AppetizeApp } from '../api/types/app';
import { Client, ClientEvents, DeviceInfo } from './client';
import { Logger } from '../logger';
import {
    Session,
    SessionConfig,
    SessionEvents,
    SessionInfo,
    UserSessionConfig,
} from '../session';
import { SocketProtocol } from '../api/types/socket';
import { waitFor } from '../waitFor';
import { AppetizeWindowProtocol } from '../window';
import { parseConfig } from '../config';

/**
 * Any client that interacts with the /embed Appetize.io page (embed, playwright)
 */
export class HeadfulClient<
    TSocket extends SocketProtocol,
    TEvents extends HeadfulClientEvents<TSession>,
    TSession extends Session<SessionEvents>
> extends Client<TSocket, TEvents, TSession> {
    device!: HeadfulDeviceInfo;
    app?: AppetizeApp;

    /**
     * an embed can only have 1 session at a time
     * but it can be started either by user clicking on the iframe,
     * or by calling client.startSession(). for either case,
     * we store that session reference here
     */
    protected session: TSession | undefined;
    protected window: AppetizeWindowProtocol;
    protected ready = false;

    /**
     * True if the client is currently requesting a session (in queue / waiting for connection)
     */
    protected isRequestingSession = false;

    private _lastSetConfigCallId: string | null = null;

    constructor({
        socket,
        window,
        logger = new Logger(),
        config,
        autoInit = true,
    }: {
        autoInit?: boolean;
        socket: TSocket;
        window: AppetizeWindowProtocol;
        logger?: Logger;
        config?: SessionConfig;
    }) {
        super({ socket, logger });
        this.window = window;

        if (config) {
            this.assignConfig(config);
        }

        this.window.on('*', async ({ type, value }) => {
            if (this.ready) {
                switch (type) {
                    case 'app':
                        this.app = value;
                        this.emit(type, value);
                        break;
                    case 'deviceInfo':
                        this.device = value;
                        this.emit(type, value);
                        break;
                    case 'config':
                        this.assignConfig(value);
                        break;
                }
            }
        });

        this.window.on('reinit', () => {
            this.ready = false;
            this.session = undefined;

            this.init({ isReinit: true });
        });

        this.socket.on('*', async ({ type, value }) => {
            if (this.ready) {
                switch (type) {
                    // when newSession is received, create the session instance
                    case 'newSession': {
                        try {
                            this.isRequestingSession = false;
                            this.session = this.createSession(this._config!, {
                                path: value.path,
                                token: value.sessionToken,
                            });
                            await this.waitForSessionStart(this.session);
                            this.emit('session', this.session);
                        } catch (e) {
                            this.session = undefined;
                            this.emit('sessionError', e);
                        }
                    }
                }
            }
        });

        if (autoInit !== false) {
            this.init();
        }
    }

    protected async init(
        args: {
            isReinit?: boolean;
        } = { isReinit: false }
    ) {
        await this.window.waitUntilReady();

        const setConfig = async () => {
            if (args.isReinit) {
                const oldConfig = this._config;

                // get the new config from the window
                const newConfig = await this.setConfig({});

                // shallowly merge the old config with the new config
                return this.setConfig({
                    record: true,
                    ...oldConfig,
                    ...newConfig,
                });
            } else {
                return this.setConfig({
                    // always set record config unless explicitly set to false
                    record: true,
                    ...this._config,
                });
            }
        };
        const [app, deviceInfo] = await Promise.all([
            this.window.postMessage({ type: 'getApp' }, true),
            this.window.postMessage<HeadfulDeviceInfo>(
                { type: 'getDeviceInfo' },
                true
            ),
            setConfig(),
        ]);

        this.app = app;
        this.device = deviceInfo;
        // this.setConfig already sets this._config

        this.ready = true;
    }

    async waitUntilReady() {
        if (this.ready) {
            return;
        }

        return waitFor(async () => {
            if (!this.ready) {
                throw new Error('Timed out waiting for client to be ready');
            }
        }, 30000);
    }

    async startSession(config?: Partial<UserSessionConfig>) {
        const wasRequestingSession = this.isRequestingSession;

        // startSession can be cancelled at any point by calling endSession, so whenever we await we'll
        // need to check if we're still requesting a session
        const wasCancelled = () => this.isRequestingSession === false;
        this.isRequestingSession = true;

        try {
            // wait until client is ready
            try {
                await this.waitUntilReady();
            } catch (e) {
                const message = e instanceof Error ? e.message : e;
                throw new Error(`Failed to start session. ${message}`);
            }

            if (!wasCancelled()) {
                if (this.session) {
                    await this.session.end();
                } else if (wasRequestingSession) {
                    await this.cancelSessionRequest();

                    // cancelSessionRequest will set this to false so we need to set to true again
                    this.isRequestingSession = true;
                }
            }

            // send through config
            if (!wasCancelled()) {
                await this.setConfig(config ?? {});
            }

            // wait for session to be requested and received
            if (!wasCancelled()) {
                // request session and wait for `session` event to be emitted off self, then resolve
                const [session] = await Promise.all([
                    new Promise<TSession>((resolve, reject) => {
                        const handleResolve = (data: TSession) => {
                            this.off('session' as any, handleResolve);
                            this.off('sessionError' as any, handleReject);
                            this.off('error' as any, handleReject);
                            resolve(data);
                        };

                        const handleReject = (data: any) => {
                            this.off('session' as any, handleResolve);
                            this.off('sessionError' as any, handleReject);
                            this.off('error' as any, handleReject);

                            if (data instanceof Error) {
                                reject(data);
                            } else if (
                                data &&
                                typeof data.message === 'string'
                            ) {
                                reject(
                                    new Error(
                                        `Session failed to start - ${data.message}`
                                    )
                                );
                            } else {
                                reject(data);
                            }
                        };

                        this.on('session' as any, handleResolve);
                        this.on('sessionError' as any, handleReject);
                        this.on('error' as any, handleReject);
                    }),
                    this.window.postMessage({ type: 'requestSession' }, true),
                ]);

                return session;
            } else {
                throw new Error('Session request was cancelled');
            }
        } finally {
            this.isRequestingSession = false;
        }
    }

    async endSession() {
        if (this.session) {
            await this.session.end();
        } else if (this.isRequestingSession) {
            await this.cancelSessionRequest();
        }
    }

    /**
     * @deprecated Use client.setConfig()
     */
    async config(args: Partial<UserSessionConfig>): Promise<UserSessionConfig> {
        // TODO: remove in v2
        this.logger.warn(
            `client.config() is deprecated and will be removed in a future major release. Use client.setConfig() instead.`
        );
        return this.setConfig(args);
    }

    async setConfig({
        // buildId & publicKey are not part of config object but we accept them
        // as parameters so we can load a new app & set config in one call
        buildId,
        publicKey,
        ...config
    }: Partial<UserSessionConfig>): Promise<SessionConfig> {
        this._lastSetConfigCallId = Math.random().toString(36);
        const currentSetConfigCallId = this._lastSetConfigCallId;

        if (!buildId && publicKey) {
            // TODO: we are just going to silently support publicKey since 100% of projects are going to be
            // using it. After a month or so (?) of v2, we'll enable this warning.
            // this.logger.warn.once(
            //     `'publicKey' is deprecated and will be removed in a future release. Please rename 'publicKey' to 'buildId'. \n More info: https://docs.appetize.io/platform/embedding-apps`
            // );
            buildId = publicKey;
        }

        if (buildId) {
            const response = await this.window.postMessage<
                AppetizeApp | { error: string }
            >(
                {
                    type: 'loadApp',
                    value: buildId,
                },
                true
            );

            if (response && 'error' in response) {
                // loadApp was cancelled from consecutive setConfig({ buildId }) calls before the app was loaded.
                // silently return the current config as we can expect another setConfig call to be in-progress
                if (response.error === 'cancelled') {
                    return this._config!;
                }

                throw new Error(response.error);
            }

            // additional check for multiple setConfig calls, can be removed once frontend responds with 'cancelled' error
            if (currentSetConfigCallId !== this._lastSetConfigCallId) {
                return this._config!;
            }
        }

        const validatedConfig = await this.window.postMessage<SessionConfig>(
            {
                type: 'setConfig',
                value: this.validateConfig({
                    ...config,
                    // embed takes publicKey for this payload still
                    ...(buildId ? { publicKey: buildId } : {}),
                }),
            },
            true
        );

        return this.assignConfig(validatedConfig);
    }

    /**
     * Updates internal _config and carries over buildId/publicKey from previous config if not defined in new config
     *
     * We carry it over because buildId/publicKey is not part of the config as far as the embed is concerned,
     * so the relayed response won't contain it. The SDK keeps it in the config as a convention for the user.
     * Eventually, we should have the embed reflect this behaviour as well.
     */
    protected assignConfig(config: UserSessionConfig) {
        if (config.autoplay === true) {
            this.logger.warn.once(
                'autoplay=true may cause the session to start before the SDK is ready. You should start the session programmatically using client.startSession() instead.'
            );
        }

        this._config = parseConfig(config);

        return this._config;
    }

    /**
     * Runs during this.setConfig() before sending config options to the embed. Override this
     * to add default or validate config values
     */
    protected validateConfig(config: Partial<UserSessionConfig>) {
        return config;
    }

    private async cancelSessionRequest() {
        if (this.isRequestingSession) {
            this.isRequestingSession = false;
            await this.window.postMessage('endSession');
            this.emit('sessionEnded');
        }
    }

    /**
     * Called by either client.startSession() or when user starts a session manually.
     * This should simply assign this.session to a new Session instance
     */
    protected createSession(
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        config: SessionConfig,
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        info: SessionInfo
    ): TSession {
        throw new Error('Not implemented');
    }
}

export interface HeadfulClientEvents<TSession extends Session>
    extends ClientEvents {
    session: TSession;
    app: AppetizeApp;
    deviceInfo: HeadfulDeviceInfo;
    sessionRequested: void;
    sessionEnded: void;
}

export interface HeadfulDeviceInfo extends DeviceInfo {
    embed: {
        width: number;
        height: number;
        screen: {
            width: number;
            height: number;
            offset: {
                x: number;
                y: number;
            };
        };
    };
}
