import {
    BrowserContext,
    PlaywrightTestArgs,
    PlaywrightTestOptions,
    PlaywrightWorkerArgs,
    PlaywrightWorkerOptions,
    test as base,
    TestInfo,
    TestType,
} from '@playwright/test';
import { PlaywrightClient } from './client';
import './expect';
import { PlaywrightSession, PlaywrightSessionConfig } from './session';
import { AppetizePage } from './appetize-page';
import { logger } from './logger';
import fs from 'fs';
import JMuxer from 'jmuxer';
import { Readable } from 'stream';
import os from 'os';
import path from 'path';
import {
    createAndroidDeviceBrowserPage,
    disconnectAndroidDevice,
} from './device-browser-page';

export interface AppetizeTestOptions extends PlaywrightTestOptions {
    /**
     * The session config to use for the tests
     */
    config: Partial<PlaywrightSessionConfig>;
}

interface TestArgs extends PlaywrightTestArgs, AppetizeTestOptions {
    appetizePage: AppetizePage;
    session: PlaywrightSession;
    client: PlaywrightClient;
}

interface WorkerArgs extends PlaywrightWorkerArgs, PlaywrightWorkerOptions {
    _browserContext: BrowserContext;
}

let appetizePage: AppetizePage | null = null;
let lastConfig: Partial<PlaywrightSessionConfig> | null = null;

const _test = base.extend<
    TestArgs & {
        _autoSnapshotSuffix: void;
        _doSetupAndTeardown: void;
        _useConfig: Partial<PlaywrightSessionConfig>;
    },
    WorkerArgs
>({
    // shallow merges _useConfig with project.use.config
    config: [
        async ({ _useConfig }, use, testInfo) => {
            const config = {
                ...(testInfo.project.use as AppetizeTestOptions).config,
                ..._useConfig,
                autoplay: false,
            };

            // handle configs with publicKey
            if ('publicKey' in config && !!config.publicKey) {
                if ('buildId' in config && !!config.buildId) {
                    logger.warn(
                        `Both "buildId" ("${config.buildId}") and "publicKey" ("${config.publicKey}") were defined in the test config. Using buildId.`
                    );
                    config.publicKey = config.buildId;
                } else {
                    config.buildId = config.publicKey;
                }
            }

            await use(config);
        },
        { auto: true },
    ],

    async page({ context, config, baseURL }, use, testInfo) {
        if (isAppetize(testInfo)) {
            appetizePage ??= await setupAppetizePage(context, config, baseURL);
            const session = await setupSession(appetizePage, config);
            if (session.config.platform !== 'android') {
                throw new Error(
                    'Appetize browser tests are currently only supported on Android devices.'
                );
            }
            const { page, browserContext, device } =
                await createAndroidDeviceBrowserPage(session, testInfo);
            try {
                await use(page);
                await browserContext.close();
            } finally {
                await disconnectAndroidDevice(device);
            }
            return;
        }
        const newPage = await context.newPage();
        await use(newPage);
    },

    async appetizePage({ context, config, baseURL }, use, testInfo) {
        if (!isAppetize(testInfo)) {
            throw new Error(
                'Appetize config not found in test. Either define it in the Playwright project or with test.use({ config: { ... } })'
            );
        }

        if (appetizePage) {
            const { session, page } = appetizePage;
            if (session) {
                let shouldClose = false;
                if (lastConfig) {
                    for (const key in config) {
                        if (
                            JSON.stringify(lastConfig[key]) !==
                            JSON.stringify(config[key])
                        ) {
                            shouldClose = true;
                            break;
                        }
                    }
                } else {
                    shouldClose = true;
                }

                if (shouldClose) {
                    if (session) {
                        await session.end().catch(() => {});
                    }
                    await page.close();
                    appetizePage = null;
                }
            }
        }

        if (!appetizePage) {
            appetizePage = await setupAppetizePage(context, config, baseURL);
        }

        lastConfig = config;
        await use(appetizePage);
    },
    async session({ appetizePage, config, _doSetupAndTeardown }, use) {
        // referencing this in the fixture will trigger
        // our setup and teardown behavior
        // for both passes and fails
        _doSetupAndTeardown;
        const session = await setupSession(appetizePage, config);
        await use(session);
    },
    async client({ appetizePage }, use) {
        await use(appetizePage.client);
    },

    // test.use({ config }) will write to this
    _useConfig: [{}, { option: true }],

    // trim platform (of computer) from snapshot suffix
    _autoSnapshotSuffix: [
        async ({}, use, testInfo) => {
            testInfo.snapshotSuffix = '';
            await use();
        },
        { auto: true },
    ],

    _doSetupAndTeardown: [
        async ({ appetizePage }, use, testInfo) => {
            logger.clearLogHistory();

            if (appetizePage) {
                appetizePage.onTestStart();
            }

            await use();

            try {
                // incase test.afterEach() wasn't called because user never made a test.afterEach()
                await saveAttachmentsOnFail({ appetizePage, testInfo });
            } catch (e) {
                logger.error(`Failed to save attachments`, e);
            }

            if (testInfo.status === 'timedOut') {
                if (appetizePage.queueTime) {
                    logger.error(
                        `Test timed out while in queue for a device. You may increase your test timeout to account for higher queue times.`
                    );
                }
            }

            try {
                removeHiddenSteps(testInfo);
            } catch (e) {
                // no-op if this breaks
            }
        },
        { auto: false },
    ],
    context: [
        async ({ _browserContext, video, contextOptions }, use) => {
            // warn about video recording
            if (video && video !== 'off') {
                logger.warn.once(
                    `Video recording is not yet supported with @appetize/playwright. Use tracing instead to view recordings of your tests https://docs.appetize.io/javascript-sdk/playwright/trace-viewer`
                );
            }

            // warn about context options
            if (Object.keys(contextOptions).length) {
                logger.warn.once(
                    `contextOptions are not supported with @appetize/playwright`
                );
            }

            await use(_browserContext);
        },
        {
            scope: 'test',
        },
    ],
    // we want the context to last across all tests
    _browserContext: [
        async ({ browser }, use) => {
            const context = await browser.newContext();
            await use(context);
            await context.close();
        },
        {
            scope: 'worker',
        },
    ],
});

// maps test.use({ config: ... }) to write to _useConfig, which will get merged with project.use.config
// output as config in the test args
const makeUse = (test: TestType<TestArgs, WorkerArgs>) => {
    const use = test.use;
    return (options: AppetizeTestOptions) => {
        const { config, ...rest } = options;
        return use({
            ...rest,
            // @ts-ignore
            _useConfig: config,
        });
    };
};

// when a test fails, we want to immediately gather data (screenshots, video, etc)
// from the session. afterEach() will always run *after* fixture teardowns, so we need
// to insert our own. (see https://playwright.dev/docs/test-fixtures#execution-order)
const makeAfterEach = (test: TestType<TestArgs, WorkerArgs>) => {
    const afterEach = test.afterEach;
    return (fn: any) => {
        afterEach(async ({ appetizePage }, testInfo) => {
            if (appetizePage) {
                try {
                    await saveAttachmentsOnFail({ appetizePage, testInfo });
                } catch (e) {
                    logger.error(`Failed to save attachments`, e);
                }
            }
        });

        return afterEach(fn);
    };
};

const makeExtend = (test: TestType<TestArgs, WorkerArgs>) => {
    const extend = test.extend;
    return (options: any) => {
        const extended = extend(options);

        // we need to re-wrap the extended fixture
        extended.afterEach = makeAfterEach(extended).bind(extended);
        extended.use = makeUse(extended).bind(extended);
        extended.extend = makeExtend(extended).bind(extended) as any;

        return extended;
    };
};

Object.assign(_test, {
    use: makeUse(_test),
    afterEach: makeAfterEach(_test),
    extend: makeExtend(_test),
    setup(
        this: TestType<TestArgs, WorkerArgs>,
        options: AppetizeTestOptions['config']
    ) {
        if (!process.env.__APPETIZE__SETUP_WARNED) {
            logger.warn(
                `test.setup() is deprecated and will be removed in a future release. Use test.use({ config: {...} }) instead`
            );
            process.env.__APPETIZE__SETUP_WARNED = 'true';
        }

        return test.use({ config: options });
    },
});

export const test = _test as TestType<TestArgs, WorkerArgs>;

export type AppetizeTestFixture = typeof test;

const outputCache = new WeakMap();

async function saveAttachmentsOnFail(args: {
    appetizePage: AppetizePage;
    testInfo: TestInfo;
}) {
    const { appetizePage, testInfo } = args;

    const session = appetizePage.session;

    const shouldSaveOutput =
        testInfo.status === 'failed' ||
        testInfo.status === 'timedOut' ||
        testInfo.status === 'interrupted';

    // i don't know why, but fixture teardowns will run after beforeAll with testInfo.status = 'pass'
    // so return early if we're not in a failed state (we only want the output data on fail anyway)
    if (!shouldSaveOutput) return;

    // this may get called multiple times, bail if we've already saved output
    if (outputCache.get(testInfo.fn)) return;
    outputCache.set(testInfo.fn, true);

    const tmpDir = path.join(
        os.tmpdir(),
        `appetize-playwright-${testInfo.testId}-${testInfo.workerIndex}`
    );

    const removeTmpDir = async () => {
        if (fs.existsSync(tmpDir)) {
            await fs.promises
                .rm(tmpDir, {
                    recursive: true,
                    force: true,
                })
                .catch(() => {
                    // noop
                });
        }
    };

    try {
        // remove dir if it already exists
        await removeTmpDir();

        await fs.promises.mkdir(tmpDir);
        const actionLogs = appetizePage.sessionDebugInfo.playedActions;

        const saveScreenshot = async () => {
            try {
                if (session) {
                    const screenshot = await session.screenshot('buffer');
                    const screenshotPath = path.join(tmpDir, 'screenshot.png');
                    await fs.promises.writeFile(
                        screenshotPath,
                        Buffer.from(screenshot.data)
                    );
                    await testInfo.attach('screenshot', {
                        path: screenshotPath,
                        contentType: 'image/png',
                    });
                }
            } catch (e) {
                logger.error(
                    `Failed to attach screenshot.png`,
                    e instanceof Error ? e.message : undefined
                );
            }
        };

        const saveActions = async () => {
            try {
                const actions = actionLogs.map((log) => {
                    if ('screenshot' in log) {
                        const { screenshot, ...r } = log;
                        return r;
                    }

                    return log;
                });

                if (!actions.length) {
                    return;
                }

                const content = JSON.stringify(actions, null, 2);
                const actionsPath = path.join(tmpDir, 'actions.json');

                await fs.promises.writeFile(actionsPath, content);
                await testInfo.attach('actions', {
                    path: actionsPath,
                    contentType: 'application/json',
                });
            } catch (e) {
                logger.error(
                    `Failed to attach actions.json`,
                    e instanceof Error ? e.message : undefined
                );
            }
        };

        const saveH264Video = async () => {
            try {
                // NOTE: we currently cannot mux in audio due to lack of timestamps, the tracks wouldnt be in sync
                const { videoFrames } = appetizePage.sessionDebugInfo;

                if (
                    videoFrames.length === 0 ||
                    videoFrames.some((f) => f.codec !== 'h264')
                ) {
                    return;
                }

                const mode = 'video';

                const jmuxer = new JMuxer(
                    // @ts-expect-error - bad 3rd party types
                    {
                        mode,
                        fps: 15,
                    }
                );

                const mp4 = await new Promise<Buffer>((res, rej) => {
                    const tm = setTimeout(() => {
                        rej(new Error('Timed out muxing session video'));
                    }, 30000);

                    const reader = new Readable({
                        objectMode: true,
                        read() {},
                    });

                    const video = Buffer.concat(
                        videoFrames.map((frame) => Buffer.from(frame.buffer))
                    );

                    reader.push({ video });
                    reader.push(null);

                    let mp4 = Buffer.from([]);

                    reader
                        .pipe(jmuxer.createStream())
                        .on('data', (d) => {
                            mp4 = Buffer.concat([mp4, d]);
                        })
                        .on('error', (e) => {
                            clearTimeout(tm);
                            rej(e);
                        })
                        .on('end', () => {
                            clearTimeout(tm);
                            res(mp4);
                        });
                });

                const mp4Path = path.join(tmpDir, 'video.mp4');

                await fs.promises.writeFile(mp4Path, mp4);

                await testInfo.attach('video.mp4', {
                    path: mp4Path,
                    contentType: 'video/mp4',
                });
            } catch (e) {
                logger.error(
                    `Failed to attach video.mp4`,
                    e instanceof Error ? e.message : undefined
                );
            }
        };

        const saveSDKLogs = async () => {
            if (!logger.logHistory.length) {
                return;
            }

            try {
                const content = `${logger.logHistory
                    .map(
                        (log) =>
                            `[${log.method}] ${JSON.stringify(log.data).slice(
                                2,
                                -2
                            )}`
                    )
                    .join('\n')}`;

                const logsPath = path.join(tmpDir, 'sdk-logs.txt');

                await fs.promises.writeFile(logsPath, content);
                await testInfo.attach('sdk-logs', {
                    path: logsPath,
                    contentType: 'application/text',
                });
            } catch (e) {
                logger.error(`Failed to attach sdk-logs.txt`, e);
            }
        };

        const saveDebugLogs = async () => {
            const { debugLogs } = appetizePage.sessionDebugInfo;
            if (debugLogs.length > 0) {
                try {
                    const content = debugLogs.join(''); // logs include \n already

                    const logsPath = path.join(tmpDir, 'debug-logs.txt');

                    await fs.promises.writeFile(logsPath, content);
                    await testInfo.attach('debug-logs', {
                        path: logsPath,
                        contentType: 'application/text',
                    });
                } catch (e) {
                    logger.error(`Failed to attach debug-logs.txt`, e);
                }
            }
        };

        const saveSession = async () => {
            try {
                if (appetizePage.session) {
                    const infoPath = path.join(tmpDir, 'session.json');
                    const content = JSON.stringify(
                        {
                            path: appetizePage.session.path,
                            token: appetizePage.session.token,
                            config: appetizePage.session.config,
                        },
                        null,
                        2
                    );

                    await fs.promises.writeFile(infoPath, content);
                    await testInfo.attach('session', {
                        path: infoPath,
                        contentType: 'application/json',
                    });
                }
            } catch (e) {
                logger.error(`Failed to attach session.json`, e);
            }
        };

        await Promise.all([
            saveScreenshot(),
            saveActions(),
            saveH264Video(),
            saveDebugLogs(),
            saveSession(),
        ]);

        // make sure we save sdk logs last, so we can capture any error messages from the above
        await saveSDKLogs();
    } finally {
        await removeTmpDir();
    }
}

/**
 * Remove hidden steps from the test info object
 *
 * Playwright does not support hidden steps so we use empty titles to mark a hidden step. This
 * is mainly used for socket messages (sent using page.evaluate) that aren't relevant to the test (requestKeyFrame, etc)
 */
function removeHiddenSteps(parent) {
    const steps = parent.steps || parent._steps;

    if (!steps) {
        return;
    }

    steps.forEach((step, i) => {
        if (step.title === '') {
            steps.splice(i, 1);
        }
        removeHiddenSteps(step);
    });
}

/**
 * Check if the project is using Appetize
 * @param testInfo The test info object
 */
function isAppetize(testInfo: TestInfo): boolean {
    return !!(testInfo.project.use as AppetizeTestOptions).config;
}

async function setupAppetizePage(
    context: BrowserContext,
    config: Partial<PlaywrightSessionConfig>,
    baseURL: string | undefined
) {
    if (!config.buildId) {
        throw new Error(
            'Appetize buildId not set. Make sure you have set it with `test.use({ config: { buildId: "..." } })` or in your Playwright config file.'
        );
    }

    const appetizePage = new AppetizePage({
        page: await context.newPage(),
        testFixture: test,
        config,
        baseURL,
    });

    await appetizePage.init();
    return appetizePage;
}

async function setupSession(
    appetizePage: AppetizePage,
    config: Partial<PlaywrightSessionConfig>
) {
    let session = appetizePage.session;
    if (!session) {
        session = await test.step(
            'Start Appetize session',
            async () => {
                await appetizePage.start({ config });

                if (appetizePage.queueTime) {
                    test.info().annotations.push({
                        type: 'queueTime',
                        description: `${appetizePage.queueTime}ms`,
                    });
                }

                if (!appetizePage.session) {
                    throw new Error('Appetize session failed to start');
                }

                return appetizePage.session;
            },
            { box: true }
        );

        test.info().annotations.push({
            type: 'Appetize Documentation',
            description: 'https://docs.appetize.io/testing',
        });
    }

    session.testFixture = test;
    return session;
}
