import { Page } from '@playwright/test';
import {
    HeadfulClient,
    HeadfulClientEvents,
} from '../../core/client/headful-client';
import {
    SessionConfig,
    SessionInfo,
    UserSessionConfig,
} from '../../core/session';
import { logger } from './logger';
import { PlaywrightSession } from './session';
import { AppetizeWindow, PlaywrightSocket } from './socket';
import { AppetizeTestFixture } from './fixture';

/**
 * AppetizeWindow can only be instantiated once per page,
 * so if a client is reinitialized a second time,
 * we need to reuse the window instance
 **/
const windows = new WeakMap<Page, AppetizeWindow>();

export class PlaywrightClient extends HeadfulClient<
    PlaywrightSocket,
    PlaywrightClientEvents,
    PlaywrightSession
> {
    page: Page;
    window!: AppetizeWindow;
    testFixture: AppetizeTestFixture;

    constructor({
        page,
        testFixture,
    }: {
        page: Page;
        testFixture: AppetizeTestFixture;
    }) {
        const window =
            windows.get(page) ?? new AppetizeWindow({ page, testFixture });
        windows.set(page, window);

        const socket = new PlaywrightSocket({
            type: 'webserver',
            page,
            window,
        });
        super({ socket, window, logger, autoInit: false });
        this.window = window;
        this.page = page;
        this.testFixture = testFixture;

        let isInQueue = false;

        this.on('queue', (queue) => {
            if (!isInQueue) {
                isInQueue = true;
                if (queue.type === 'concurrent') {
                    this.logger.log(
                        `Entered ${queue.name}. Please wait until a slot becomes available.`
                    );
                } else {
                    this.logger.log(
                        `All devices are currently in use. Please wait until requested device becomes available.`
                    );
                }
            }

            if (queue.position > 0) {
                if (queue.type === 'concurrent') {
                    this.logger.log(
                        `Position in ${queue.name}: ${queue.position}`
                    );
                } else {
                    this.logger.log(`Position in queue: ${queue.position}`);
                }
            }
        });

        this.on('session', () => {
            if (isInQueue) {
                isInQueue = false;
            }
        });
    }

    async init() {
        return this.testFixture.step('Initialize client', async () => {
            await this.window.init();
            await super.init();
        });
    }
    protected validateConfig(config: UserSessionConfig) {
        const browserType = this.page.context().browser()?.browserType().name();
        return {
            codec: browserType === 'chromium' ? 'jpeg' : 'h264',
            ...config,
        };
    }

    protected createSession(config: SessionConfig, sessionInfo: SessionInfo) {
        this.session = new PlaywrightSession({
            config,
            page: this.page,
            window: this.window,
            path: sessionInfo.path,
            token: sessionInfo.token,
            device: this.device,
            app: this.app,
            testFixture: this.testFixture,
        });

        return this.session;
    }
}

export interface PlaywrightClientEvents
    extends HeadfulClientEvents<PlaywrightSession> {}
