import { Page } from '@playwright/test';
import { SessionConfig } from '../../core/session';
import { retry } from '../../core/util';
import { PlaywrightClient } from './client';
import { PlaywrightSession, PlaywrightSessionConfig } from './session';
import { queryString } from '../../core/util';
import { HeadfulDeviceInfo } from '../../core/client/headful-client';
import {
    PlayActionResult,
    PlayActionErrorResponse,
} from '../../core/api/types/recorder';
import { AppetizeTestFixture } from './fixture';

export class AppetizePage {
    readonly page: Page;
    readonly testFixture: AppetizeTestFixture;

    session?: PlaywrightSession;
    client!: PlaywrightClient;
    queueStart: number | null = null;
    queueEnd: number | null = null;
    config: PlaywrightSessionConfig;
    baseURL = 'https://appetize.io';

    sessionDebugInfo = {
        playedActions: [] as ActionLog[],
        videoFrames: [] as Array<{
            buffer: Uint8Array;
            codec: 'h264' | 'jpeg';
        }>,
        audioFrames: [] as Array<{ buffer: Uint8Array; codec: 'aac' }>,
        debugLogs: [] as string[],
    };

    constructor(args: {
        page: Page;
        testFixture: AppetizeTestFixture;
        config: PlaywrightSessionConfig;
        baseURL?: string;
    }) {
        this.page = args.page;
        this.testFixture = args.testFixture;
        this.config = {
            ...args.config,
            buildId: args.config.buildId ?? args.config.publicKey,
            // only define publicKey if it's set
            ...(args.config.publicKey
                ? { publicKey: args.config.publicKey }
                : {}),
        };

        if (args.baseURL) {
            this.baseURL = args.baseURL;
        }
    }

    get queueTime() {
        if (this.queueStart) {
            return (this.queueEnd ?? Date.now()) - this.queueStart;
        }

        return null;
    }

    async init() {
        const params = {};

        for (const key in this.config) {
            switch (key) {
                case 'device':
                case 'osVersion':
                case 'scale':
                case 'orientation':
                    params[key] = this.config[key];
                    break;
            }
        }

        const pageRes = await this.page.goto(
            `${this.baseURL}/embed/${this.config.buildId}?${queryString(
                params
            )}`
        );

        if (pageRes?.status() === 404) {
            const label = this.config.publicKey ? 'publicKey' : 'buildId';
            throw new Error(
                `App not found for ${label} "${
                    this.config.publicKey ?? this.config.buildId
                }"`
            );
        }

        this.client = new PlaywrightClient({
            page: this.page,
            testFixture: this.testFixture,
        });

        await this.client.init();
    }

    async start(args: { config: SessionConfig }) {
        this.queueStart = null;
        this.queueEnd = null;

        this.client.on('queue', () => {
            if (!this.queueStart) {
                this.queueStart = Date.now();
            }
        });

        this.client.on('queueEnd', () => {
            if (this.queueStart) {
                this.queueEnd = Date.now();
            }
        });

        const resizeToDevice = async (info?: HeadfulDeviceInfo) => {
            if (info?.embed) {
                await this.page.setViewportSize({
                    // add some padding to prevent scrollbars from showing on chromium
                    width: info.embed.width + 8,
                    height: info.embed.height + 8,
                });
            }
        };
        this.client.on('deviceInfo', resizeToDevice);

        await this.client.waitUntilReady();

        // set initial size
        if (this.client.device) {
            resizeToDevice(this.client.device);
        }

        const session = await retry(
            () => this.client.startSession(args.config),
            {
                retries: 5,
                timeout: 30000,
                predicate: (e, attempt) => {
                    if (
                        e instanceof Error &&
                        e.message.match(/too many requests/)
                    ) {
                        console.warn(
                            `Too many session requests. Retrying in 30 seconds... (attempt #${attempt})`
                        );
                        return true;
                    }

                    return false;
                },
            }
        );

        this.config = session.config;

        // startSession resolves slightly after stream has started, so request a keyframe
        this.requestKeyFrame();

        if (session.config.debug === true) {
            session.on('log', (value) => {
                this.sessionDebugInfo.debugLogs.push(value.message);
            });
        }

        session.on('playbackFoundAndSent', (value) => {
            this.sessionDebugInfo.playedActions.push(value);
        });

        session.on('playbackError', (value) => {
            this.sessionDebugInfo.playedActions.push(value);
        });

        session.on('video', (frame) => {
            this.sessionDebugInfo.videoFrames.push(frame);
        });

        session.on('audio', (frame) => {
            this.sessionDebugInfo.audioFrames.push(frame);
        });

        session.on('end', () => {
            this.session = undefined;
        });

        this.session = session;

        return session;
    }

    onTestStart() {
        this.clearSessionDebugInfo();
        this.requestKeyFrame();
    }

    private clearSessionDebugInfo() {
        this.sessionDebugInfo.playedActions = [];
        this.sessionDebugInfo.videoFrames = [];
        this.sessionDebugInfo.audioFrames = [];
        this.sessionDebugInfo.debugLogs = [];
    }

    private requestKeyFrame() {
        if (this.session) {
            this.testFixture.step(/* hidden step */ '', async () => {
                return (
                    this.session?.socket
                        .send('requestKeyFrame')
                        // don't log error if this fails
                        .catch(() => {})
                );
            });
        }
    }
}

type ActionLog = PlayActionErrorResponse | PlayActionResult;
