import { Page } from '@playwright/test';
import type {
    Action,
    ElementSelector,
    PlayActionErrorResponse,
    PlayActionOptions,
    PlayActionResult,
    RecordedAction,
} from '../../core/api/types/recorder';
import { FindElementsAction } from '../../core/api/types/recorder/internal';
import { OperationalError, captureOperationalError } from '../../core/errors';
import {
    Session,
    SessionArgs,
    SessionConfig,
    SessionEvents,
} from '../../core/session';
import {
    WaitForEventOptionsOrPredicate,
    waitForEvent,
    waitForTimeout,
} from '../../core/waitFor';
import { Codegen } from './codegen';
import { logger } from './logger';
import { AppetizeWindow, PlaywrightSocket } from './socket';
import { AllUI } from '../../core/api/types/recorder/public';
import { AppetizeTestFixture } from './fixture';

interface ActionLog {
    action: Action | RecordedAction;
    result?: PlayActionResult;
    payload?: any;
    error?: PlayActionErrorResponse;
    ui?: AllUI;
}

export interface PlaywrightSessionEvents extends SessionEvents {}

export interface PlaywrightSessionConfig extends SessionConfig {
    buildId?: string;

    /**
     * @deprecated use buildId instead
     */
    publicKey?: string;
}
export class PlaywrightSession extends Session {
    page: Page;
    window: AppetizeWindow;
    testFixture: AppetizeTestFixture;

    config: SessionConfig;
    protected actionLogs: Array<ActionLog> = [];

    constructor({
        page,
        config,
        window,
        testFixture,
        ...args
    }: Omit<SessionArgs, 'socket' | 'logger'> & {
        page: Page;
        config: SessionConfig;
        window: AppetizeWindow;
        testFixture: AppetizeTestFixture;
    }) {
        const socket = new PlaywrightSocket({
            page,
            window,
            type: 'appetizer',
        });
        super({ ...args, socket, config, logger });
        this.window = window;
        this.page = page;
        this.config = config;
        this.testFixture = testFixture;

        // disconnect session on page navigation
        // is there a better event to listen for? 'framenavigated' event fires too soon
        this.page.on('load', () => {
            this.emit('disconnect');
        });

        // Wrap our methods as steps to be shown in playwright trace
        // with proper titles
        this.adbShellCommand = this.wrapAsStep(this.adbShellCommand);
        this.biometry = this.wrapAsStep(this.biometry);
        this.findElement = this.wrapAsStep(this.findElement, {
            subStepTitle: (selector) => JSON.stringify(selector),
        });
        this.findElements = this.wrapAsStep(this.findElements, {
            subStepTitle: (selector) => JSON.stringify(selector),
        });
        this.getUI = this.wrapAsStep(this.getUI);
        this.keypress = this.wrapAsStep(this.keypress, {
            subStepTitle: (key) => JSON.stringify(key),
        });
        this.launchApp = this.wrapAsStep(this.launchApp, {
            subStepTitle: (app) => JSON.stringify(app),
        });
        this.openUrl = this.wrapAsStep(this.openUrl, {
            subStepTitle: (url) => JSON.stringify(url),
        });
        this.playAction = this.wrapAsStep(this.playAction, {
            subStepTitle: (action) => JSON.stringify(action),
        });
        this.playActions = this.wrapAsStep(this.playActions, {
            subStepTitle: (actions) => JSON.stringify(actions),
        });
        this.reinstallApp = this.wrapAsStep(this.reinstallApp);
        this.restartApp = this.wrapAsStep(this.restartApp);
        this.rotate = this.wrapAsStep(this.rotate, {
            subStepTitle: (direction) => JSON.stringify(direction),
        });
        this.screenshot = this.wrapAsStep(this.screenshot);
        this.setLanguage = this.wrapAsStep(this.setLanguage, {
            subStepTitle: (language) => JSON.stringify(language),
        });
        this.setLocation = this.wrapAsStep(this.setLocation, {
            subStepTitle: (location) => JSON.stringify(location),
        });
        this.shake = this.wrapAsStep(this.shake);
        this.swipe = this.wrapAsStep(this.swipe, {
            subStepTitle: (args) => JSON.stringify(args),
        });
        this.tap = this.wrapAsStep(this.tap, {
            subStepTitle: (args) => JSON.stringify(args),
        });
        this.type = this.wrapAsStep(this.type, {
            subStepTitle: (text) => JSON.stringify(text),
        });
        this.waitForAnimations = this.wrapAsStep(this.waitForAnimations);
        this.waitForElement = this.wrapAsStep(this.waitForElement, {
            subStepTitle: (selector) => JSON.stringify(selector),
        });
        this.waitForEvent = this.wrapAsStep(this.waitForEvent, {
            title: (event) => `session.waitForEvent - ${JSON.stringify(event)}`,
        });
        this.waitForTimeout = this.wrapAsStep(this.waitForTimeout, {
            title: (ms) => `session.waitForTimeout - ${JSON.stringify(ms)}`,
        });
    }

    protected async addActionLog(log: ActionLog): Promise<void> {
        if (log.error) {
            const ui = await this.getUI().catch(this.logger.warn);

            if (ui) {
                log.ui = ui;
            }
        }

        this.actionLogs.push(log);
    }

    async rotate(direction: 'left' | 'right') {
        try {
            const [orientation] = await Promise.all([
                waitForEvent<'landscape' | 'portrait'>(
                    this.window,
                    'orientationChanged'
                ),
                // we need to use postmessage api so that embed knows which direction we rotated.
                // otherwise, if we emit directly over the socket, frontend only knows that orientation changed
                // from/to portrait/landscape, not the actual rotation in degrees
                await this.window.postMessage(
                    direction === 'left' ? 'rotateLeft' : 'rotateRight'
                ),
            ]);

            this.window.page.waitForTimeout(1000);

            return orientation;
        } catch (e) {
            captureOperationalError(e, this.rotate);
            throw e;
        }
    }

    async record() {
        try {
            if (!this.config.record) {
                throw new OperationalError(
                    'Recording is not enabled, please enable it by setting `record: true` in session config'
                );
            }

            if (!this.testFixture) {
                throw new OperationalError(
                    'session.record() requires using `session` from the test() arguments'
                );
            }

            const codegen = new Codegen({
                session: this,
                testInfo: this.testFixture.info(),
            });

            return codegen.record();
        } catch (e) {
            captureOperationalError(e, this.record);
            throw e;
        }
    }

    async waitForEvent<K extends keyof SessionEvents>(
        event: K,
        options?: WaitForEventOptionsOrPredicate<SessionEvents[K]>
    ): Promise<SessionEvents[K]> {
        try {
            return await waitForEvent(this, event, options);
        } catch (e) {
            captureOperationalError(e, this.waitForEvent);
            throw e;
        }
    }

    async waitForTimeout(ms: number) {
        try {
            return await waitForTimeout(ms);
        } catch (e) {
            captureOperationalError(e, this.waitForTimeout);
            throw e;
        }
    }

    /**
     * Waits for the element(s) to appear or else throws an error.
     */
    async waitForElement(
        element: ElementSelector,
        options?: PlayActionOptions<FindElementsAction> & {
            /**
             * The number of elements for this selector that should be returned
             */
            matches?: number;
        }
    ) {
        try {
            const elements = await this.findElements(element, options);

            if (elements.length) {
                if (options?.matches && elements.length !== options.matches) {
                    throw new Error(
                        `Expected ${options.matches} elements, found ${elements.length}`
                    );
                }

                return elements;
            } else {
                throw new Error(
                    `Element not found:\n${JSON.stringify(element)}`
                );
            }
        } catch (e) {
            captureOperationalError(e, this.waitForElement);
            throw e;
        }
    }

    on<K extends keyof SessionEvents>(
        event: K,
        listener: (value: SessionEvents[K]) => void
    ): this {
        return super.on(event, listener);
    }

    once<K extends keyof SessionEvents>(
        event: K,
        listener: (value: SessionEvents[K]) => void
    ): this {
        return super.once(event, listener);
    }

    async getVideoFrames(): Promise<never[]> {
        console.warn(
            `getVideoFrames() is deprecated. Use session.on('video') event instead`
        );
        return [];
    }

    async getAudioFrames(): Promise<never[]> {
        console.warn(
            `getAudioFrames() is deprecated. Use session.on('audio') event instead`
        );
        return [];
    }

    // returns a bound version of the function that will run as a step
    private wrapAsStep<T extends (...args: any[]) => any>(
        fn: T,
        options: {
            title?: (...args: Parameters<T>) => string;

            /**
             * Playwright doesn't offer a great way to add additional detail like params,
             * so we can add a substep title to provide more context when expanded and keep things clean.
             */
            subStepTitle?: (...args: Parameters<T>) => string;
        } = {}
    ) {
        return (...args: Parameters<T>) => {
            let title = `session.${fn.name}`;

            if (options?.title) {
                title = options.title(...args);
            }

            const subStepTitle = options?.subStepTitle?.(...args);

            if (subStepTitle) {
                return this.testFixture.step(
                    title,
                    () =>
                        this.testFixture.step(subStepTitle, () =>
                            fn.apply(this, args)
                        ),
                    { box: true }
                );
            } else {
                return this.testFixture.step(
                    title,
                    () => fn.apply(this, args),
                    { box: true }
                );
            }
        };
    }
}
