import { exec } from 'child_process';
import { _android as android } from 'playwright-core';
import { promisify } from 'util';
import { PlaywrightSession } from './session';
import { AndroidDevice, TestInfo } from '@playwright/test';
import fs from 'fs';
import { logger } from './logger';

const execAsync = promisify(exec);
const readFileAsync = promisify(fs.readFile);
const writeFileAsync = promisify(fs.writeFile);

/**
 * Create a Chrome browser page on an Appetize Android device.
 * Pre-requisites:
 * - Session must be configured with `enableAdb: true`.
 * - Must have the [adb daemon](https://developer.android.com/studio/command-line/adb) installed & running.
 * - No other connected devices.
 *
 * @param session The Appetize Playwright session
 * @param testInfo The test info object
 * @returns the running Chrome Browser page, BrowserContext, and Android device.
 */
export async function createAndroidDeviceBrowserPage(
    session: PlaywrightSession,
    testInfo: TestInfo
) {
    const adbInfo = session.adbConnection;
    const host = 'localhost';
    const port = 6000;

    if (!adbInfo) {
        throw new Error(
            'ADB connection info not found. Session must be configured with `enableAdb: true`'
        );
    }

    await checkAdbInstalled();

    // Disconnect any existing ADB connections
    try {
        await execAsync(`adb disconnect ${host}:${port}`);
    } catch (e) {
        // no-op
    }

    const sshCmd = `ssh -fN -o StrictHostKeyChecking=no -oHostKeyAlgorithms=+ssh-rsa -p ${adbInfo.port} ${adbInfo.user}@${adbInfo.hostname} -L${port}:${adbInfo.forwards[0].destination}:${adbInfo.forwards[0].port}`;
    const sshOutput = await execAsync(sshCmd);

    if (sshOutput.stdout) {
        logger.error(sshOutput.stdout);
    }

    if (sshOutput.stderr) {
        logger.error(sshOutput.stderr);
    }

    const adbCmd = `adb connect ${host}:${port}`;
    const adbOutput = await execAsync(adbCmd);

    if (adbOutput.stdout) {
        logger.error(adbOutput.stdout);
    }

    if (adbOutput.stderr) {
        logger.error(adbOutput.stderr);
    }

    // Connect to the Android device
    const device = await android
        .devices()
        .then((devices) =>
            devices.find((device) => device.serial() === `${host}:${port}`)
        );

    if (!device) {
        throw new Error(`Device ${host}:${port} not found`);
    }

    logger.debug(
        `Connected to device ${device.model()} with serial ${device.serial()}`
    );

    // launchBrowser overwrites the command line file, so we need to read
    // what is there before launching the browser and pass it into the args option
    const flags = await getExistingChromeCommandLineFlags(testInfo);

    const browserContext = await device.launchBrowser({
        args: flags,
    });

    // launchBrowser removes the cmd line file afterwards, so we want to restore our original incase
    // the browser is restarted
    await pushChromeCommandLine(testInfo, `chrome ${flags.join(' ')}`);

    const pages = browserContext.pages();

    // use existing page if available. it seems there is no difference in either using
    // this page or calling newPage(), but sometimes newPage() throws an error which
    // (might just be a playwright bug w/ android)
    if (pages[0]) {
        return {
            page: pages[0],
            browserContext,
            device,
        };
    }

    const page = await browserContext.newPage();
    return { page, browserContext, device };
}

/**
 * Disconnects and closes the running Android device.
 * @param device Device to disconnect
 */
export async function disconnectAndroidDevice(device: AndroidDevice) {
    await device.close();
    await execAsync(`adb disconnect ${device.serial()}`);
}

async function getExistingChromeCommandLineFlags(
    testInfo: TestInfo
): Promise<string[]> {
    const testOutputDir = testInfo.outputPath();
    const pulledPath = testInfo.outputPath('chrome-command-line');

    const chromeCommandLinePath = '/data/local/tmp/chrome-command-line';

    let flags: string[] = [];

    // get the file if it exists
    try {
        await execAsync(`adb pull ${chromeCommandLinePath} ${testOutputDir}`);
        const file = await readFileAsync(pulledPath, 'utf-8');

        if (file) {
            const flagsString = file.split('chrome ').at(1);

            if (flagsString) {
                flags = flagsString.split(' ') || [];
            }
        }

        // the permissions for this file on our emulator will prevent playwright from writing to it,
        // but we can work around it by pushing it and that will correct the permissions
        await execAsync(`adb push ${pulledPath} ${chromeCommandLinePath}`);
    } catch (e) {
        // no-op
    }

    return flags;
}

async function pushChromeCommandLine(testInfo: TestInfo, content: string) {
    const pulledPath = testInfo.outputPath('chrome-command-line');

    const chromeCommandLinePath = '/data/local/tmp/chrome-command-line';

    try {
        await writeFileAsync(pulledPath, content);
        await execAsync(`adb push ${pulledPath} ${chromeCommandLinePath}`);
    } catch (e) {
        logger.error('Error pushing chrome-command-line file', e);
    }
}

async function checkAdbInstalled() {
    try {
        await execAsync('adb version');
    } catch (e) {
        throw new Error(
            'ADB is not installed or not found in PATH. Please install ADB and ensure it is accessible.'
        );
    }
}
