import { PlaywrightSession } from '../session';
import { TestInfo } from '@playwright/test';
import type {
    RecordedAction,
    TypeAction,
} from '../../../core/api/types/recorder';
import fs from 'fs';
import { waitForTimeout } from '../../../core/waitFor';
import pause from './pause';
import { getKeyFromKeypress, isSpecialKey } from '../../../core/keys';

export class Codegen {
    private session: PlaywrightSession;
    private testInfo: TestInfo;
    private currentRecord = 0;

    constructor({
        testInfo,
        session,
    }: {
        testInfo: TestInfo;
        session: PlaywrightSession;
    }) {
        this.session = session;
        this.testInfo = testInfo;
    }

    async record() {
        const file = this.testInfo.file;
        const contents = await fs.promises.readFile(file, 'utf8');

        // get session.record() statements for this test
        const recordStatements = contents
            .split('\n')
            // attach line numbers to each line
            .map((line, num) => ({ line, num: num + 1 }))
            // remove up to the current test() line #
            .slice(this.testInfo.line)
            // filter out lines that don't contain session.record()
            .filter(({ line }) => line.includes('session.record()'));

        const currentLine = recordStatements[this.currentRecord].num;

        if (currentLine !== undefined) {
            console.log(` Recording at line ${currentLine}`);

            const actions: any[] = [];

            const handleAction = (action: RecordedAction) => {
                concatActions(actions, action);
                console.log(describeAction(action));
            };

            this.session.on('action', handleAction);

            // playwright shows the codecalling the pause, so we'll jump them
            // to another file with some helpful comments
            await pause(this.session.page);

            // wait a few seconds to catch any other actions
            await waitForTimeout(2000);

            this.session.off('action', handleAction);

            const newContents = contents.split('\n').map((line, index) => {
                if (index === currentLine - 1) {
                    const tabs = line.match(/^\s*/)?.[0] ?? 0;

                    // generates a comment to summarize the recorded events
                    const descriptions = actions
                        .map((action) => {
                            return describeAction(action);
                        })
                        .reduce(
                            (acc, cur, i) => `${acc}\n// ${i + 1}. ${cur}`,
                            '// Recorded using session.record()'
                        );

                    return `${descriptions}\nawait session.playActions(${JSON.stringify(
                        actions,
                        null,
                        '\t'
                    )})`
                        .split('\n')
                        .map((line) => tabs + line)
                        .join('\n');
                }
                return line;
            });
            await fs.promises.writeFile(file, newContents.join('\n'));

            console.log(` Finished`);
            this.currentRecord += 1;
        }
    }
}

/**
 * Concatenates consecutive actions of the sam etype into single ones when applicable
 */
function concatActions(
    actions: Array<RecordedAction | TypeAction>,
    action: RecordedAction
) {
    const lastAction = actions[actions.length - 1];

    if (lastAction) {
        switch (action.type) {
            case 'keypress': {
                // join consecutive keypress events into a single action
                if (
                    lastAction?.type === 'keypress' &&
                    !isSpecialKey(action.key) &&
                    !isSpecialKey(lastAction.key)
                ) {
                    actions.pop();

                    actions.push({
                        type: 'typeText',
                        text:
                            getKeyFromKeypress(lastAction) +
                            getKeyFromKeypress(action),
                    });
                } else if (
                    lastAction?.type === 'typeText' &&
                    !isSpecialKey(action.key)
                ) {
                    actions.pop();

                    actions.push({
                        type: 'typeText',
                        text: lastAction.text + getKeyFromKeypress(action),
                    });
                } else {
                    actions.push(action);
                }

                break;
            }
            default:
                actions.push(action);
        }
    } else {
        actions.push(action);
    }
}

/**
 * Describes the action in a human readable way
 */
function describeAction(action: RecordedAction | TypeAction) {
    let on = '';

    switch (action.type) {
        case 'swipe':
        case 'tap': {
            const element = action.element;

            if (typeof element === 'string') {
                on = ` on element "${element}"`;
            } else {
                if (element?.attributes?.accessibilityIdentifier) {
                    on = `element with accessibilityIdentifier "${element.attributes?.accessibilityIdentifier}"`;
                } else if (element?.attributes?.class) {
                    on = `element with class "${element.attributes?.class}"`;
                } else if (
                    'position' in action &&
                    action.position?.x &&
                    action.position?.y
                ) {
                    on = `position ${Math.round(
                        action.position.x * 100
                    )}%, ${Math.round(action.position.y * 100)}%`;
                }
            }

            if (on) {
                return `${action.type} on ${on}`;
            } else {
                return action.type;
            }
        }
        case 'keypress':
            return `type "${getKeyFromKeypress(action)}"`;
        case 'typeText':
            return `type "${action.text}"`;
    }
}
