import { LogFn, Logger } from '../../core/logger';

/**
 * This logger will not print to the console when silent=true.
 *
 * This is good for CI to keep output clean and only print the logs
 * after a test fails. The fixture handles this logic, and by default
 * this logger is silent when process.env.CI is true.
 */
export class PlaywrightLogger extends Logger {
    logHistory: Array<{
        method: 'log' | 'warn' | 'error' | 'debug';
        data: any[];
    }> = [];

    logLevel: 'verbose' | 'warnings-errors' | 'none' =
        process.env.CI === 'true' ? 'warnings-errors' : 'verbose';

    log = this.createPlaywrightLogFn('log', () => this.logLevel === 'verbose');

    warn = this.createPlaywrightLogFn(
        'warn',
        () => this.logLevel === 'verbose' || this.logLevel === 'warnings-errors'
    );

    error = this.createPlaywrightLogFn(
        'error',
        () => this.logLevel === 'verbose' || this.logLevel === 'warnings-errors'
    );

    debug = this.createPlaywrightLogFn(
        'debug',
        () => this.logLevel === 'verbose'
    );

    clearLogHistory = () => {
        this.logHistory = [];
    };

    private createPlaywrightLogFn(
        type: 'log' | 'warn' | 'error' | 'debug',
        print: () => boolean
    ) {
        const loggedMessages = new Set<string>();
        const context = '[Appetize]';

        // preserves the original console context
        const fn: LogFn = (...data: any[]) => {
            this.logHistory.push({
                method: type,
                data,
            });

            if (print()) {
                console[type](context, ...data);
            }
        };

        // logs message only once, but we do lose the console context with this implementation
        fn.once = (msg: string) => {
            if (loggedMessages.has(msg)) {
                return;
            } else {
                loggedMessages.add(msg);
            }

            return fn.call(console, msg);
        };

        return fn;
    }
}

export const logger = new PlaywrightLogger();
