import { Page } from '@playwright/test';
import { SocketProtocol } from '../../core/api/types/socket';
import { waitFor, waitForEvent, WaitForEventOptions } from '../../core/waitFor';
import { VERSION } from '../../core/constants';
import { AppetizeWindowProtocol } from '../../core/window';
import { TimeoutError } from '../../core/errors';
import { EventEmitter } from '../../core/EventEmitter';
import { logger } from './logger';
import { AppetizeTestFixture } from './fixture';

/**
 * Communicates with Appetize /embed page
 */
export class AppetizeWindow
    extends EventEmitter
    implements AppetizeWindowProtocol
{
    page: Page;
    ready = false;
    testFixture: AppetizeTestFixture;

    constructor({
        page,
        testFixture,
    }: {
        page: Page;
        testFixture: AppetizeTestFixture;
    }) {
        super();
        this.page = page;
        this.testFixture = testFixture;
    }

    async init() {
        this.ready = false;

        await this.testFixture.step('Connect to Appetize page', async () => {
            // attach __appetize_on function
            await this.page.exposeFunction('__appetize_on', (json) => {
                const data = JSON.parse(json, function (key, value) {
                    // look for typed array flags
                    try {
                        if ('flag' in value && value.flag === 'TYPED_ARRAY') {
                            return global[value.constructor].from(
                                value.data.split(',').map(Number)
                            );
                        }
                    } catch (e) {
                        // no-op
                    }

                    return value;
                });

                const type = data === 'string' ? data : data.type;
                const value = data.value;

                this.emit(type, value);
                this.emit('*', { type, value });
            });

            await this.page.evaluate(
                async ([VERSION]) => {
                    return new Promise<boolean>((resolve, reject) => {
                        const timeout = setTimeout(() => {
                            clearInterval(interval);
                            reject(
                                new TimeoutError(
                                    `Timed out after 60000ms waiting for connection to Appetize page`
                                )
                            );
                        }, 60000);

                        const interval = setInterval(() => {
                            const channel = new MessageChannel();

                            channel.port1.onmessage = () => {
                                clearInterval(interval);
                                clearTimeout(timeout);
                                channel.port1.close();
                                channel.port2.close();
                                resolve(false);
                            };
                            window.postMessage(
                                {
                                    type: 'init',
                                    appetizeClient: true,
                                    version: VERSION,
                                },
                                '*',
                                [channel.port2]
                            );
                            window.__appetize_postMessage = async (
                                data,
                                waitForResponse = false
                            ) => {
                                const channel = new MessageChannel();

                                window.postMessage(data, '*', [channel.port2]);

                                if (waitForResponse) {
                                    return new Promise((resolve, reject) => {
                                        const tm = setTimeout(() => {
                                            reject(
                                                new TimeoutError(
                                                    'Timed out after 60000ms while waiting for postMessage response'
                                                )
                                            );
                                        }, 60000);

                                        channel.port1.onmessage = (ev) => {
                                            clearTimeout(tm);
                                            channel.port1.close();
                                            channel.port2.close();
                                            resolve(ev.data as any);
                                        };
                                    });
                                } else {
                                    channel.port1.close();
                                    channel.port2.close();
                                }
                            };
                        }, 100);
                    });
                },
                [VERSION]
            );

            // call exposed function on client/session events
            await this.page.evaluate(() => {
                window.addEventListener('message', (event) => {
                    if (event.source === window) {
                        const eventType =
                            typeof event.data === 'string'
                                ? event.data
                                : event.data?.type;

                        switch (eventType) {
                            // we'll get these from socket xdoc events directly.
                            // save some bandwidth by not sending them twice
                            case 'frameData':
                            case 'recordedAction':
                            case 'playbackFoundAndSent':
                            case 'playbackNotFound':
                            case 'debug':
                            case 'interceptRequest':
                            case 'interceptResponse':
                            case 'interceptError':
                            case 'uiDump':
                                return;
                        }

                        window.__appetize_on(
                            JSON.stringify(event.data, function (key, value) {
                                // typed arrays will be serialized to normal arrays, so
                                // we flag them as typed arrays for the parser to correctly convert them back
                                // modified from https://gist.github.com/jonathanlurie/04fa6343e64f750d03072ac92584b5df
                                if (
                                    value instanceof Int8Array ||
                                    value instanceof Uint8Array ||
                                    value instanceof Uint8ClampedArray ||
                                    value instanceof Int16Array ||
                                    value instanceof Uint16Array ||
                                    value instanceof Int32Array ||
                                    value instanceof Uint32Array ||
                                    value instanceof Float32Array ||
                                    value instanceof Float64Array
                                ) {
                                    const replacement = {
                                        constructor: value.constructor.name,
                                        data: value.join(','),
                                        flag: 'TYPED_ARRAY',
                                    };
                                    return replacement;
                                }
                                return value;
                            })
                        );
                    }
                });
            }, []);
        });

        this.ready = true;
    }

    /**
     * Waits for page to be loaded and ready for xdoc messages
     */
    async waitUntilReady() {
        return waitFor(async () => {
            if (!this.ready) {
                throw new TimeoutError(
                    `Timed out after 60000ms while waiting for Appetize window to be ready.`
                );
            }
        }, 60000);
    }

    /**
     * Sends a raw postMessage to the window
     */
    async postMessage<T>(data: any, waitForResponse = false) {
        await this.waitUntilReady();
        try {
            return await this.testFixture.step('postMessage', async () =>
                this.page.evaluate(
                    async ([data, waitForResponse]) => {
                        return window.__appetize_postMessage(
                            data,
                            waitForResponse
                        ) as T;
                    },
                    [data, waitForResponse]
                )
            );
        } catch (e) {
            logger.error('Error sending postMessage: ', data);
            throw e;
        }
    }
}

export class PlaywrightSocket extends EventEmitter implements SocketProtocol {
    page: Page;
    type: 'webserver' | 'appetizer';
    window: AppetizeWindow;

    constructor({
        page,
        type,
        window,
    }: {
        page: Page;
        type: 'webserver' | 'appetizer';
        window: AppetizeWindow;
    }) {
        super();
        this.page = page;
        this.type = type;
        this.window = window;

        this.window.on('*', ({ type, value }) => {
            switch (type) {
                case 'socketEvent':
                    if (value.socket === this.type) {
                        const socketEventType = value.type;
                        const socketEventValue = value.value;

                        this.emit(socketEventType, socketEventValue);
                        this.emit('*', {
                            type: socketEventType,
                            value: socketEventValue,
                        });
                    }
                    break;
                case 'disconnect':
                    this.emit('disconnect');
                    this.emit('*', { type: 'disconnect' });
                    break;

                // map xdocs to appetizer events
                case 'sessionInfo':
                case 'chromeDevToolsUrl':
                case 'orientationChanged':
                case 'deviceInfo':
                    if (this.type === 'appetizer') {
                        this.emit(type, value);
                        this.emit('*', { type, value });
                    }
                    break;

                // map xdocs to client events
                case 'sessionRequested':
                    if (this.type === 'webserver') {
                        this.emit(type, value);
                        this.emit('*', { type, value });
                    }
                    break;
            }
        });
    }

    async send(event: string, data?: any) {
        return this.window.postMessage<void>({
            type: 'emitSocketEvent',
            value: { type: event, value: data, socket: this.type },
        });
    }

    async disconnect() {
        await this.send('disconnect');
    }

    waitForEvent<T>(
        event: string,
        options: WaitForEventOptions<T>
    ): Promise<T> {
        return waitForEvent(this, event, options);
    }
}

declare const window: Window & {
    __appetize_postMessage: (
        data: any,
        waitForResponse?: boolean
    ) => Promise<void>;
    __appetize_on: (data: any) => void;
};
