import { KeypressAction } from './api/types/recorder';

export const controlKeys = [8, 9, 13, 16, 37, 38, 39, 40, null];
export const digitCharsWithShift = [
    ')',
    '!',
    '@',
    '#',
    '$',
    '%',
    '^',
    '&',
    '*',
    '(',
];
export const charsWithShift = {
    47: '?',
    44: '<',
    45: '_',
    46: '>',
    91: '{',
    92: '|',
    93: '}',
    96: '~',
    59: ':',
    61: '+',
    39: '"',
};

export const keysWithShift = {
    191: '?',
    188: '<',
    189: '_',
    190: '>',
    219: '{',
    220: '|',
    221: '}',
    192: '~',
    186: ':',
    187: '+',
    222: '"',
};

export function convertCharToRawKey(key: string): {
    key: string;
    shiftKey: boolean;
} {
    let result;
    for (const entry in charsWithShift) {
        if (key === charsWithShift[entry]) {
            result = {
                // @ts-expect-error - copied from angular, entry should be a number but not sure if this works/is used
                key: String.fromCharCode(entry),
                shiftKey: 'true',
            };
            return result;
        }
    }
    const indexInDigits = digitCharsWithShift.indexOf(key);
    if (indexInDigits > -1) {
        result = {
            key: String.fromCharCode(indexInDigits + 48).toLowerCase(),
            shiftKey: true,
        };
    } else if (key !== key.toLowerCase()) {
        result = {
            key: key.toLowerCase(),
            shiftKey: true,
        };
    } else {
        result = {
            key: key,
            shiftKey: false,
        };
    }
    return result;
}

export function getKeyFromKeypress(keypress: KeypressAction) {
    if (keypress.character) {
        return keypress.character;
    }

    // if no character, key must exist
    const key = keypress.key!;

    if (isSpecialKey(key)) {
        return key;
    }

    return keypress.shiftKey ? key.toUpperCase() : key.toLowerCase();
}

export function isSpecialKey(key: string) {
    const pattern = /^[\b\t\r]/;

    return pattern.test(key);
}

export function isSpecialChar(char: string) {
    return char.length > 0;
}
/**
 * Converts legacy keycode to new keycode
 */
export function mapLegacyKeyCode(key: string) {
    switch (key) {
        case 'home':
            return 'HOME';
        case 'volumeUp':
            return 'VOLUME_UP';
        case 'volumeDown':
            return 'VOLUME_DOWN';
    }

    return key;
}

/**
 * Converts new keycode to legacy keycode
 */
export function mapKeyCodeToLegacy(key: string) {
    switch (key) {
        case 'HOME':
            return 'home';
        case 'VOLUME_UP':
            return 'volumeUp';
        case 'VOLUME_DOWN':
            return 'volumeDown';
    }

    return key;
}
