/* eslint-disable @typescript-eslint/no-namespace */
import { expect } from '@playwright/test';
import {
    Element,
    ElementSelector,
    FindElementsAction,
    PlayActionOptions,
} from '../../core/api/types/recorder';
import { PlaywrightSession } from './session';

declare global {
    namespace PlaywrightTest {
        interface Matchers<R> {
            toHaveElement(
                selector: ElementSelector,
                options?: ToHaveElementOptions
            ): Promise<R>;
        }
    }
}

interface ToHaveElementOptions extends PlayActionOptions<FindElementsAction> {
    /**
     * The number of elements for this selector that should be returned
     */
    matches?: number;
}

expect.extend({
    toHaveElement: async (
        session: PlaywrightSession,
        element: Element,
        options: ToHaveElementOptions = {}
    ) => {
        try {
            const elements = await session.findElements(element, options);

            const pass =
                typeof options.matches === 'number'
                    ? elements.length === options.matches
                    : elements.length > 0;

            return {
                pass,
                message: () =>
                    `Element not found:\n${JSON.stringify(element, null, 2)}`,
            };
        } catch (e) {
            return {
                pass: false,
                message: () => (e as Error).message,
            };
        }
    },
});
